import Category from './CateEnum'

// 保存一条便签数据 
class ItemData {
    // id!: number;
    // categoryId!: Category;
    // title!: string;
    // content!: string;
    createTime!: string;

    // 在构造函数的参数上使用public等同于创建了同名的成员变量
    constructor (public id:number = -1,public categoryId:Category = -1, public title:string = '', public content: string = '') {
        this.id = id;
        this.categoryId = categoryId;
        this.title = title;
        this.content = content;
        this.createTime = this.toSelfDateStr(Date.now());
    }
    // 将当 时间戳 转成日期时间字符串
    toSelfDateStr(timeSpan: number): string{
        let date = new Date(timeSpan);
        let timeStr = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' +
        date.getHours() + ':' + (date.getMinutes() > 9 ? date.getMinutes() : "0" + date.getMinutes());
        return timeStr;
    }
}

export default ItemData;