/* -- 负责将便签数据保存到localStorage-- */
import ItemData from '../model/ItemData';
import Category from '@/model/CateEnum';

class DataHelper {
    dataKey: string;        //localStoragr中的key
    primaryKey: string;     //每条数据对象的主键名(ID)
    memoList:Array<ItemData>;
    number!:number;

    constructor() {
        this.dataKey = "memoList";
        this.primaryKey = "id";
        this.memoList = this.read();
        this.number = this.memoList.length;
    }
    memoLength():number{
        // console.log('获取长度');
        // console.log(this.memoList.length);
       
        return  this.memoList.length;
    }
    read():any{
        // 读取本地数据 并将读取的json数组字符串 转成数组对象
        let strData: string | null = localStorage.getItem(this.dataKey);       
        let arrData:any = strData ? JSON.parse(strData) : [];  
        
        // 数组中的每条对象 转换成 ItemData类型 数组
        if(arrData.length > 0){          
            let data = this.readItemData(arrData);         
            return data;     
        } else {    
            return arrData;
        }
    }
    readItemData(arrData:any):Array<ItemData> {

        let memoArr = arrData.map( (memoItem:any) => {
            let item:ItemData = new ItemData();  // 默认itemData对象
            // 已存储的数据覆盖默认对象
            item.id = memoItem.id;
            item.categoryId = memoItem.categoryId;
            item.title = memoItem.title;
            item.content = memoItem.content;
            item.createTime = memoItem.createTime;
            // let item:ItemData = Object.assign(new ItemData(),memoItem);
            return item;
        })
             
        return memoArr;
    }

    // 存入数据
    saveData(arrData:Array<ItemData>): void{    
        // 数组转成JSON字符串
        let str:string = JSON.stringify(arrData); 
        localStorage.setItem(this.dataKey,str);
    }

    // 新增数据
    addData(newDataObj:ItemData){
        // 获取已经存储的数据
        let dataArray = this.read();
        
        // 设置新增数据id
        let itemId = dataArray.length > 0 ? dataArray[dataArray.length-1][this.primaryKey]+1 : 1;
        newDataObj.id = itemId;

        dataArray.push(newDataObj);
        // 更新memolist
        this.memoList.push(newDataObj);
        // 更新便签长度
        // this.number = this.memoLength();
        // this.number = this.memoList.length;
        this.number = this.memoList.length
        console.log("addDataNumber"+this.number);
        
     
        this.saveData(dataArray);

        // 返回新增的数据ID
        return itemId;
    }
    // 修改一条笔记
    edit(item: ItemData): void {
        let editItem: ItemData | undefined = this.memoList.find((memoItem) =>{
            return item.id == memoItem.id
        });
        if (editItem) {
            editItem.categoryId = item.categoryId;
            editItem.title = item.title;
            editItem.content = item.content;

            //将更新后的 数组 重新保存到 本地localstorage
            this.saveData(this.memoList);
        }
    }
    // 删除一条数据
    removeDataById(id:string | number): boolean {
        let dataArray = this.memoList;
        // 查找数据中ID一样的数据并删除
        let delIndex = dataArray.findIndex((item:any) => item.id == id);
        if(delIndex > -1){
            dataArray.splice(delIndex,1);
            this.saveData(dataArray);
            return true;
        } else {
            return false;
        }
    }
    // 全部删除
    removeAll () {
        localStorage.removeItem(this.dataKey);
    }
    getCategoryName(cateId: Category): string {
        const arrNames = ['工作', '生活', '学习'];
        return arrNames[cateId];
    }
}

export default DataHelper;